.calculation {
  .container {
    position: relative;
  }
  
  &-row {
    display: flex;
    align-items: flex-start;
  }
  
  &-form {
    width: 626px;
    min-width: 626px;
    background-color: #363A4D;
    border-radius: 20px;
    padding: 50px 48px 61px;
    box-shadow: 15px 5px 40px rgba(9, 19, 73, 0.2784313725);
  }
  &-form .col {
    padding: 0 15px;
  }
  #category{
    border-radius: 10px;
    padding: 16px 20px;
    border: none;
    max-width: 100%;
    transition: all 0.5s;
}
  .col.controled-col {
    padding-top: 20px;
  }
  
  .input-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }
  .input-wrapper.controled-wrapped {
    margin-bottom: 8px;
  }
  .input-wrapper.file-wrapper {
    padding-top: 46px;
  }
  
  label {
    font-size: 15px;
    font-weight: 600;
    line-height: 126%;
    margin-bottom: 6px;
  }
  
  input, [type=file] + label {
    border-radius: 10px;
    padding: 16px 20px;
    border: none;
    max-width: 100%;
    transition: all 0.5s;
  }
  input:hover, [type=file] + label:hover {
    box-shadow: 0px 0px 2px 2px #E58D35;
  }
  input:focus, [type=file] + label:focus {
    box-shadow: 0px 0px 2px 2px #89C249;
  }
  input:active, [type=file] + label:active {
    box-shadow: 0px 0px 10px 0px #89C249;
  }
  input:focus-visible, [type=file] + label:focus-visible {
    outline: none;
  }
  input::placeholder, [type=file] + label::placeholder {
    font-size: 15px;
    line-height: 126%;
    color: #cbcbcb;
  }
  input[type=file], [type=file] + label[type=file] {
    display: none;
  }
  
  [type=file] + label {
    border: 1px dashed rgba(255, 255, 255, 0.2196078431);
    border-radius: 10px;
    padding: 11px 14px;
    text-align: center;
  }
  
  .file-name {
    position: absolute;
    left: 0;
    top: 12px;
    font-size: 15px;
  }
  
  &-decoration {
    margin: 0px 0 -113px -28px;
    height: auto;
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 560px;
    width: 30vw;
  }

}