.template-card {
    background-color: #363A4D;
    padding: 0 20px 27px;
    max-width: 360px;
    min-height: 483px;
    border-radius: 20px;
    text-align: center;
    overflow: hidden;
  }
  .template-card > .img-box {
    height: 229px;
    margin: 0 -20px 27px;
    display: flex;
  }
  .template-card > .img-box img {
    object-fit: cover;
    object-position: center;
  }
  .template-card__template {
    margin-bottom: 12px;
  }
  .template-card__subtitle {
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 14px;
  }
  .template-card__options {
    display: flex;
    justify-content: center;
    gap: 35px;
    margin-bottom: 7px;
  }
  .template-card__options-item {
    display: flex;
    gap: 7px;
  }
  .template-card__options-item .img-box {
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .template-card__price {
    margin-bottom: 11px;
  }
  .template-card__price span {
    font-size: 22px;
  }
  .template-card__price span:first-child {
    color: #A3A3A3;
  }
  .template-card__price strong {
    font-size: 46px;
  }
  .template-card-btn-row {
    display: flex;
    gap: 14px;
  }
  .template-card-btn-row .btn {
    width: 50%;
    justify-content: center;
    padding: 13px 10px;
  }
  
  .slider-template {
    margin: 0 -15px;
  }
  .slider-template .slick-slide {
    margin: 0 15px;
  }
  .slider-template .template-card {
    margin: 0 auto;
  }
  .slider-template .slick-arrow {
    position: absolute;
    z-index: 1;
    top: calc(50% - 20px);
  }
  .slider-template .slick-prev {
    left: -100px;
    transform: translate(0, -50%) rotate(180deg);
  }
  .slider-template .slick-next {
    right: -100px;
    transform: translate(0, -50%);
  }
  
  .services {
    padding-top: 76px;
    padding-bottom: 100px;
  }
  .services .template-card {
    text-align: left;
    position: relative;
  }
  .services .template-card:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: linear-gradient(180deg, rgba(186, 186, 186, 0.2) 0%, rgba(0, 0, 0, 0.8117647059) 100%);
    z-index: 1;
  }
  .services .template-card > .img-box img {
    position: absolute;
    left: 0;
    height: 100%;
    width: 100%;
    top: 0;
  }
  .services .template-card .template-card__title,
  .services .template-card .template-card__subtitle,
  .services .template-card .template-card-btn-row {
    position: relative;
    z-index: 2;
  }
  