.advantages, .services {
    padding: 112px 0 60px;
}

.slider-advantages {
    margin: -24px 0;

}

.advantages .container {
    max-width: 1820px;
    background-color: #363A4D;
    border-radius: 20px;
    margin: 0 auto;
    padding: 80px;
}

.slider-advantages {
  margin: -24px 0;
}

.advantages-item {
    text-align: center;
    margin: 24px 0;
}

.advantages-img-wrapper {
  width: 113px;
  height: 113px;
  border: 1px solid #6c7188;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 22px;
  padding: 10px;
  border-radius: 50%;
}

.advantages-img {
  background-color: #89C249;
  width: 93px;
  height: 93px;
  object-fit: scale-down;
  border-radius: 50%;
}

.advantages-title {
  font-weight: 400;
  max-width: 200px;
  margin: 0 auto;
}
