.footer {
    position: relative;
  }
  .footer .container {
    margin-bottom: 50px;
    padding-top: 100px;
  }
  .footer .container .transportation-footer ul > li,
  .footer .container .services-footer ul > li,
  .footer .container .info-footer ul > li {
    margin-bottom: 5px;
  }
  .footer .container a {
    color: #85899C;
    line-height: 160.5%;
  }
  .footer .container a:hover {
    color: #E58D35;
    transition: 0.3s;
  }
  .footer .container ul {
    padding-left: 0;
  }
  .footer .container ul li:before {
    content: none;
  }
  .footer .container:before {
    content: "";
    background: #E58D35;
    width: 1140px;
    height: 2px;
    position: absolute;
  }
  .footer .container .information {
    padding-top: 40px;
    display: flex;
    gap: 87px;
  }
  .footer .container .main-info {
    max-width: 263px;
  }
  .footer .container .main-info .logo-footer {
    margin-bottom: 29px;
  }
  .footer .container .main-info-footer {
    margin-bottom: 21px;
  }
  .footer .container .main-info-footer ul > li {
    font-size: 14px;
    line-height: 162.5%;
  }
  .footer .container .social-link {
    margin-right: 4px;
    text-decoration: none;
  }
  .footer .container .social-link:hover {
    opacity: 0.6;
  }
  .footer .container .title-footer {
    line-height: 146%;
    margin-bottom: 14px;
  }
  

.container {
    max-width: 1170px;
    margin-right: auto;
    margin-left: auto;
  }
  